export default {
	data() {
		return {
			user_phone: '12345678001',
			user_password: '123456'
		}
	},
	onLoad() {

	},
	methods: {
		login() {
			if(this.user_phone === '' || this.user_password === ''){
				uni.showToast({
				    title: '请完善信息',
				    duration: 2000,
						icon: 'none'
				});
			}
			uni.request({
			    url: this.$BASE_HOST+'user/login', //仅为示例，并非真实接口地址。
					method: 'POST',
			    data: {
			        userPhone: this.user_phone,
							userPassword: this.$md5(this.user_password)
			    },
			    success: (res) => {
			        console.log(res.data);
							if(res.data.code !== 20000){
								uni.showToast({
								    title: '账号或密码错误',
								    duration: 2000,
										icon: 'none'
								});
							} else {
								uni.switchTab({
								    url: '../../home/home'
								});
								uni.setStorage({
								    key: 'local_userinfo',
								    data: res.data.data
								});
							}
			    }
			});
		}
	}
}
